.social-list {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  flex-wrap: wrap;
  width: 132px;
  padding: 20px 74px 21px;
  margin: 0;
  border-bottom: 1px solid #d9d9d9;
  list-style-type: none;
}

@media (min-width: $tablet-width) {
  .social-list {
    margin: 0;
    padding: 0;
    width: 132px;
    border: none;
  }
}

@media (min-width: $desktop-width) {
  .social-list {
    margin: 0;
    padding: 0;
  }
}
